var consturl;
export default {
	data() {
		return {
			getAddress:"",
			address:"",
			openid:""
		}
	},
	onLoad(options) {
		
	},
	onShow:function(){
		let ts=this,a=[];
		consturl=ts.$consturl;
		ts.openid=uni.getStorageSync("openid");
		ts.address=uni.getStorageSync("adress") ;
		// console.log(a.push(ts.address));
		// 获取用户地址列表
		uni.request({
			url: consturl + "wx/api/getAddress", //
			data: {
				openid:ts.openid
			},
			success: function (res) {
				let b=res.data.data;
				let c=JSON.parse(b);
				ts.getAddress=c;
					console.log(ts.getAddress);
			},
			complete:function(){
			}
		});
	},
	methods: {
		// 添加地址按钮 
		addNewAddress:function(){
			uni.redirectTo({
				url: 'addNewAddress'
			});
		},
		// 删除地址信息
		deleteItem:function(i){
			let ts=this;
			ts.getAddress.splice(i,1);
			console.log(i);
			wx.request({
				url: consturl + "wx/api/setAddress", //
				data: {
					openid: ts.openid,
					address:JSON.stringify(ts.getAddress)
				},
				method: 'POST',
				header: {
				        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
				      },
				success: function(res) {
					// console.log(JSON.stringify(getAddress));
					uni.navigateTo({
						url: '#'
					});
				},
				complete: function() {
					// console.log("complete");
				}
			});
		},
		// edit:function(j){
		// 	uni.navigateTo({
		// 		url: 'addNewAddress?index='+j
		// 	});
		// }
		click:function(j){
			// 修改用户默认地址
			let ts=this;
			let gas= ts.getAddress[j];
			uni.getStorageSync("adress") 
			uni.setStorageSync("adress",gas);
		},
		save:function(){
			uni.navigateBack({
				delta:1,
			})
			
		}
		
	}
}
